import { phoneTest } from '../../common/js/util.js'
export default {
	data() {
		return {
			showPassword: true,
			showPasswordTwo: true,
			mobile:'',//手机号码
			password:'',//密码
			regpassword:'',//确认密码
			code: '' ,//验证码
			user_name:'',//后台名称
			parent_user:'',//上级代理商
			count: '',
			timer: '',
			disabled:false,
			show: true
		}
	},
	onLoad() {
		this.$store.dispatch('getUserInfo',{}).then(res=>{
			this.parent_user = res.data.uinfo.mobile
		}).catch(err=>{})
	},
	methods: {
		//返回
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		changeEyes(type){
			type==0?this.showPassword=!this.showPassword:this.showPasswordTwo=!this.showPasswordTwo
		},
		// 获取验证码
		getCode() {
			if(!this.mobile){
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
					mask: false,
					duration: 1500
				})
				return false
			}
			if(phoneTest(this.mobile,'手机号码格式不正确')) return
			this.$store.dispatch('signupCode',{
				mobile: this.mobile
			}).then(res=>{
				// 等待时长60s获取一次验证码
				const TIME_COUNT = 60;
				if (!this.timer) {
					this.count = TIME_COUNT;
					this.show = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			})
			
		},
		submit(){
			if(!this.parent_user){
				uni.showToast({
					title: '请输入正确推荐人手机号',
					icon: 'none',
					mask: false,
					duration: 1500
				})
				return false
			}
			if(!this.mobile){
				uni.showToast({
					title: '请输入注册手机号',
					icon: 'none',
					mask: false,
					duration: 1500
				})
				return false
			}
			if(!this.password){
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					mask: false,
					duration: 1500
				})
				return false
			}
			if(this.password != this.regpassword){
				uni.showToast({
					icon:'none',
					title:'输入的两次密码不一致',
					mask: false,
					duration: 1500
				})
				return false;
			}
			if(!this.code){
				uni.showToast({
					title: '请输入验证码',
					icon: 'none',
					mask: false,
					duration: 1500
				})
				return false
			}
			let data ={
				uname: this.mobile,
				upwd:this.password,
				repassword:this.regpassword,
				code: this.code,
				EXphoto:this.parent_user
			}
			this.disabled = true
			this.$store.dispatch('signup',data).then(res=>{
				uni.showToast({
					title: '注册成功',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500,
					success: (res)=>{
						setTimeout(()=>{
							this.disabled = false
							uni.navigateBack({
								delta: 1
							})
						},1500)
					}
				});
			}).catch(result=>{
				this.disabled = false
			})
		},
	}
}
